import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from 'react-bootstrap/Navbar';
import './App.css';
import NumberOfEvents from './NumberOfEvents';
import EventList from './EventList';
import CitySearch from './CitySearch';
import { extractLocations, getEvents } from './api';

import "./nprogress.css";
import { Nav } from 'react-bootstrap';

class App extends Component {
  state = {
    events: [],
    locations: [],
    selectedLocation: 'all',
    numberOfEvents: '2',
  }

  componentDidMount() {
    this.mounted = true;
    getEvents().then((events) => {
      if (this.mounted) {
        this.setState({ events, locations: extractLocations(events),
         });
      }
    });
  }

  componentWillUnmount(){
    this.mounted = false;
  }

  updateEvents = (location, numberOfEvents) => {
    this.setState({
      selectedLocation: location,
      numberOfEvents: numberOfEvents,
    });
  }

  render() {
    let {events, locations, selectedLocation, numberOfEvents } = this.state;

    return (
      <div className="main">
      <Navbar className="nav" sticky="top" variant="light">
      <Navbar.Brand className="logo" href="#">gather</Navbar.Brand>
      <Nav.Link className="nav-link" href="/">refresh</Nav.Link>
      </Navbar>
      <div className="App">
        <CitySearch locations={locations} updateEvents={this.updateEvents} />
        <EventList events={events} />
        <NumberOfEvents numberOfEvents={numberOfEvents} selectedLocation={selectedLocation} updateEvents={this.updateEvents}/>
      </div>
      </div>
    );
  }
}

export default App;